import React, { Component } from 'react'
import { Container, Col, Form, FormGroup, Label, Input, Button, Card, CardTitle, CardBody, Row } from 'reactstrap'
import './AddAssignments.css'
import Spinner from '../../../Reusables/Spinner/Spinner'
import { Redirect } from 'react-router-dom'
import Select from 'react-select'
import 'react-select/dist/react-select.css'

class AddAssignments extends Component {
  constructor () {
    super()
    this.state = {
      assignmentsPacks: [{name: '', assignments: [{input: '', output: ''}]}], // [{name, assignments:[{name, input, output}]}]
      assignmentsAmount: 1,
      testsAmount: 1,
      value: undefined,
      options: [],
      loading: false,
      redirect: false,
      redirect_url: ''
    }

    window.fetch('/api/get-teacher-categories', {
      credentials: 'include',
      method: 'POST'
    })
      .then(res => res.json())
      .then(json => {
        if (json.categories.length === 0) {
          this.setState({options: []})
        } else {
          this.setState({options: json.categories})
        }
        console.log('this.state: ', this.state)
      })
      .catch(err => {
        console.log('err when requesting /api/get-teacher-categories: ', err)
      })
  }

  sendHandler = (e) => {
    e.preventDefault()
    const data = new window.FormData(document.getElementById('mainForm'))
    data.append('file', document.getElementById('pdfTasks'))
    data.append('category', this.state.value.value)

    for (let pair of data.entries()) {
      if (pair[1] === null || pair[1] === '' || pair[1] === undefined || (this.state.value === undefined && this.state.options.length === 0)) {
        window.alert('One of form fields is not present. Fill it, before continuing.')
      }
    }

    this.setState({loading: true})

    window.fetch(`/api/add-assignment`, {
      accept: 'application/json',
      credentials: 'include',
      method: 'POST',
      body: data
    })
      .then(response => response.json())
      .then(resp => {
        console.log(resp)
        this.setState({loading: false, redirect: true, redirect_url: '/teacher/categories'})
      })
  }

  handleChange = (value) => {
    console.log('value: ', value)
    this.setState({value: value})
    console.log('state after changing: ', this.state)
  }

  renderTest = (assignmentIter) => {
    let obj = {}
    obj['input'] = ''
    obj['output'] = ''
    let newState = [...this.state.assignmentsPacks[assignmentIter].assignments, obj]
    let updatedState = this.state.assignmentsPacks
    updatedState[assignmentIter].assignments = newState
    this.setState({testsAmount: ++this.state.testsAmount,
      assignmentsPacks: updatedState
    })
    console.log('updated state: ', this.state)
    // this.forceUpdate()
  }

  render () {
    if (this.state.redirect) {
      return (
        <Redirect
          to={{
            pathname: this.state.redirect_url
          }}
        />
      )
    }

    let assignmentIter = -1

    return (this.state.loading ? <Spinner />
      : <Container>
        <Card className='pack__container'>
          <CardBody>
            <CardTitle>Add assignment pack</CardTitle>
            <Form id='mainForm' onSubmit={this.sendHandler}>
              <FormGroup row>
                <Label for='assignmentPackCategory' sm={2}>
                  Pack category
                </Label>
                <Col sm={10}>
                  <Select.Creatable
                    name='form-field-name'
                    placeholder='Select or create new one'
                    value={this.state.value}
                    onChange={this.handleChange}
                    options={this.state.options}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Label for='assignmentPackName' sm={2}>
                  Pack name
                </Label>
                <Col sm={10}>
                  <Input noValidate name='assignmentPackName' id='assignmentPackName' placeholder='Example: multidimensional arrays' />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Label for='pdfTasks' sm={2}>
                  Assignments file
                </Label>
                <Col sm={10}>
                  <Input type='file' name='pdfTasks' id='pdfTasks' />
                </Col>
              </FormGroup>
              <FormGroup>
                <Card sm={10} className='pack__container'>
                  <Row >
                    <Col sm={12} className='assignment_margin'>
                      {
                        this.state.assignmentsPacks.map(each => {
                          ++assignmentIter
                          return <div key={`container_${this.state.assignmentsAmount}-${this.state.testsAmount}`}>
                            <FormGroup row>
                              <Label for='assignmentNames' sm={{ size: 2, offset: 1 }}>
              Assignment Name
                              </Label>
                              <Col sm={8}>
                                <Input name='assignmentNames' placeholder='Example: Task' defaultValue={each.name} />
                              </Col>
                            </FormGroup>
                            <Row>{
                              each.assignments.map(e => {
                                return <Col sm={3} key={`test_input_${this.state.assignmentsAmount}-${this.state.testsAmount}}`} className='test__container test_margin'>
                                  <Label for={`test_input_${this.state.assignmentsAmount}-${this.state.testsAmount}}`}>
                              Input
                                  </Label>
                                  <Col>
                                    <Input type='textarea' name={`test_input_${this.state.assignmentsAmount}-${this.state.testsAmount}}`} />
                                  </Col>
                                  <Label for={`test_output_${this.state.assignmentsAmount}-${this.state.testsAmount}}`}>Output</Label>
                                  <Col>
                                    <Input type='textarea' name={`test_output_${this.state.assignmentsAmount}-${this.state.testsAmount}}`} />
                                  </Col>
                                  <Col>
                                    <Button className='add-test__button' onClick={() => this.renderTest(assignmentIter)}>
                                    Add test
                                    </Button>
                                  </Col>
                                </Col>
                              }
                              )
                            }</Row>
                          </div>
                        })}
                    </Col>
                  </Row>
                  <CardBody />
                  <Button onClick={() => this.renderAssignment()}> Add more assignments</Button>
                </Card>
              </FormGroup>
              <FormGroup row>
                <Col sm={{ size: 10, offset: 2 }}>
                  <Button>Submit!</Button>
                </Col>
              </FormGroup>
            </Form>
          </CardBody>
        </Card>
      </Container>
    )
  }
}

export default AddAssignments



